<template>
  <div class="container">
    <div class="upper">
      <AntMenu style="border-right: 1px solid #f6f6f6; height: 100%" :selected-keys="selectedTab">
        <AbstraLogo style="width: 160px; padding: 5px 15px" />
        <AntMenuItemGroup v-for="section in availableSections" :key="section.name">
          <template #title>{{ section.name }}</template>
          <AntMenuItem
            v-for="item in section.items"
            :key="item.name"
            role="button"
            class="menu-item"
            :disabled="item.unavailable || section.cloud"
            tabindex="0"
          >
            <template #icon>
              <Component
                :is="item.icon"
                :class="{
                  disabled: item.unavailable,
                  active: selectedTab.includes(item.path),
                }"
                size="20"
              />
            </template>
            <RouterLink
              :to="item.path"
              :class="{ active: selectedTab.includes(item.path), disabled: item.unavailable }"
            >
              <span style="margin-right: 5px">{{ item.name }}</span>
            </RouterLink>
            <AntTag v-if="item.unavailable">SOON</AntTag>
            <AntTag v-if="item.beta">BETA</AntTag>
            <Tooltip v-if="item.warning" :text="item.warning" :fixed="true" :top="18" :left="18">
              <PhSealWarning color="#D35249" size="20" />
            </Tooltip>
          </AntMenuItem>
        </AntMenuItemGroup>
      </AntMenu>
    </div>
    <div class="lower">
      <AntDivider v-if="$slots.bottom" />
      <slot name="bottom"></slot>
    </div>
  </div>
</template>

<script setup lang="ts">
import Tooltip from '@/apps/editor/components/Tooltip.vue';
import AbstraLogo from '@/components/AbstraLogo.vue';
import { PhSealWarning } from '@phosphor-icons/vue';
import {
  Divider as AntDivider,
  Menu as AntMenu,
  MenuItem as AntMenuItem,
  MenuItemGroup as AntMenuItemGroup,
  Tag as AntTag,
} from 'ant-design-vue';
import { computed, defineComponent } from 'vue';
import { useRoute } from 'vue-router';

const route = useRoute();

function getSelectedTab() {
  const routePath =
    props.sections
      .map((group) => group.items)
      .flat()
      .find((item) => {
        const url = route.path.split('/');
        const path = url[url.length - 1];
        return item.path === path;
      })?.name ?? 'forms';

  return [routePath];
}

const selectedTab = computed(getSelectedTab);

export type Section = {
  name: string;
  cloud?: boolean;
  items: {
    name: string;
    icon: ReturnType<typeof defineComponent>;
    path: string;
    unavailable?: boolean;
    beta?: boolean;
    feature?: 'databases';
    warning?: string | null;
  }[];
};

const props = defineProps<{
  sections: Section[];
}>();

const availableSections = computed(() =>
  props.sections.map((section) => ({
    ...section,
    items: section.items.filter((item) => !item.unavailable),
  })),
);
</script>

<style scoped lang="scss">
.menu-item {
  svg {
    margin-bottom: -2px;
  }
}

.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 230px;

  overflow: hidden;
  justify-content: space-between;
}

.upper {
  overflow-y: auto;
  flex-grow: 1;
  height: 100%;
}

.lower {
  background-color: white;
  border-right: 1px solid #f6f6f6;
  flex-grow: 0;
}
</style>
