<template>
  <AntAlert v-if="billingMetadata?.message" type="error">
    <template #message>
      <AntText type="secondary">{{ billingMetadata?.message }}</AntText>
    </template>
    <template #action>
      <AntButton
        v-if="billingMetadata?.messageLink"
        type="primary"
        target="_blank"
        :href="billingMetadata.messageLink"
      >
        {{ messageCTA }}
      </AntButton>
      <AntButton v-else type="primary" @click="messageClick">{{ messageCTA }}</AntButton>
    </template>
  </AntAlert>
</template>
<script lang="ts" setup>
import { Alert as AntAlert, Button as AntButton, TypographyText as AntText } from 'ant-design-vue';
import { computed } from 'vue';
import { OrganizationModel } from '../../apis/cloud';
import { Comms } from '../../apis/intercom';
import { Tracking } from '../../apis/tracking';

const props = defineProps<{
  organizationId: string;
  billingMetadata?: OrganizationModel['billingMetadata'];
}>();

const messageCTA = computed(() => props.billingMetadata?.messageCTA ?? 'Contact Us');

const messageClick = () => {
  Comms.showNewMessage('Help me with my plan');
  Tracking.billingAlertCtaClicked(props.organizationId, messageCTA.value);
};
</script>
