<template>
  <div ref="tooltipBox" class="tooltip-box" @mouseenter="shake">
    <slot />
    <div class="tooltip" :style="style">
      <span class="text">{{ text }}</span>
    </div>
  </div>
</template>
<script lang="ts" setup>
import { computed, ref, type StyleValue } from 'vue';

const shaker = ref(Date.now());

const shake = () => {
  shaker.value = Date.now();
};

const props = defineProps({
  text: {
    type: String,
    required: true,
  },
  left: {
    type: Number,
  },
  top: {
    type: Number,
  },
  fixed: {
    type: Boolean,
    default: false,
  },
});

const tooltipBox = ref<HTMLDivElement | null>(null);

const fixedPositioning = (): StyleValue => {
  const rect = tooltipBox.value?.getBoundingClientRect();
  if (!rect) return {};

  const { x, y } = rect;

  shaker.value;

  return {
    position: 'fixed',
    top: `${y + (props.top ?? 0)}px`,
    left: `${x + (props.left ?? 0)}px`,
  };
};

const style = computed<StyleValue>(() => {
  if (props.fixed) return fixedPositioning();

  return {
    left: `${props.left ?? -14}px`,
    ...(props.top ? { top: `${props.top}px` } : {}),
  };
});
</script>

<style lang="scss" scoped>
.tooltip-box {
  position: relative;
  display: inline-block;

  &:hover .tooltip {
    opacity: 1;
  }
}

.tooltip {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  text-align: center;
  padding: 4px 8px;
  border-radius: 3px;
  font-size: 10px;
  font-weight: 500;
  line-height: 12px;
  cursor: default;
  user-select: none;

  width: 77px;
  min-height: 20px;

  opacity: 0;
  transition: opacity 1s;

  position: absolute;
  z-index: 3;

  background: #4e596a;
}
</style>
